<template>
  <div class="main">
    <!-- 登录页面 -->
    <div class="login">
      <div class="formStyle">
        <el-form
          class="formNormal"
          ref="form"
          :model="loginData"
          label-width="60px"
        >
          <h2 style="margin-bottom: 15px; color: white">用户登录</h2>
          <el-form-item style="width: 460px" label="用户名">
            <el-input
              v-model="loginData.username"
              suffix-icon="el-icon-user"
              placeholder="请输入用户名"
            ></el-input>
          </el-form-item>
          <el-form-item label="密码">
            <el-input
              type="password"
              @keydown.enter.native="login"
              v-model="loginData.password"
              placeholder="请输入密码"
              show-password
            ></el-input>
            <!-- <el-button
              style="float: right; margin-left: 20px"
              @click="goChange"
              type="text"
              >修改密码?</el-button
            > -->
          </el-form-item>
          <el-form-item style="margin: 0; float: right">
            <el-button @click="goRegister" type="text">还没有账户？</el-button>
            <el-button @click="login" type="primary">登录</el-button>
          </el-form-item>
        </el-form>
      </div>
    </div>
  </div>
</template>
<script>
import { Login } from '@/api/api';
export default {
  data() {
    return {
      loginData: {
        username: '',
        password: '',
      },
    };
  },
  methods: {
    login() {
      if (this.loginData.username == '' || this.loginData.password == '') {
        this.$message({
          message: '请输入正确登录信息',
          type: 'warning',
        });
      } else {
        Login(this.loginData).then(res => {
          console.log(res);
          if (res.data.code == 1) {
            // 登录成功
            this.$message({
              message: '登录成功',
              type: 'success',
            });
            // 将登录信息存入sessionStorage中
            sessionStorage.setItem('username', this.loginData.username);
            this.$router.push('/index');
          } else if (res.data.code == 0) {
            // alert("用户不存在，请先注册");
            this.$message({
              message: '用户不存在，请先注册',
              type: 'warning',
            });
            this.$router.push('/register');
          } else {
            this.$message({
              message: '用户名或密码错误',
              type: 'error',
            });
          }
        });
      }
    },
    goChange() {
      this.$router.push('/forget');
    },
    goRegister() {
      this.$router.push('/register');
    },
  },
};
</script>
<style scoped>
.main {
  height: 100vh;
  background: url(../../assets/login/login_bg.jpg);
  background-size: cover;
  display: flex;
  justify-content: center;
  align-items: center;
}
.login {
  width: 550px;
  height: 250px;
  background-color: rgb(31, 46, 65);
  opacity: 0.8;
  display: flex;
  justify-content: center;
  align-items: center;
}
.formStyle {
  display: flex;
  justify-content: center;
  align-items: center;
  flex-wrap: wrap;
}
</style>
<style>
.login .el-form-item__label {
  color: white;
}
</style>
